import Vue from "vue";
import VueRouter from "vue-router";
import MyCV from "../views/MyCV.vue";
import Cv from "../views/Cv.vue";
import Home from "../views/Home.vue";


Vue.use(VueRouter);

const routes = [
   {
    path: "/",
    name: "cv",
    component: MyCV
  },
   {
    path: "/bot",
    name: "cv",
    component: Cv
  },
  {
    path: "/resume",
    name: "mycv",
    component: Home
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
