<template>
    <div>
    
    <transition name="fade">
  	<div v-show="showpopup"  class="chat-popup">
		<div class="chat-window">
			<div  class="chat-avatar">
			<img src="../assets/bot-avatar.png" alt="Avatar">
			
			<p class="version">beta v1.0</p>	
					
			</div>
			<div id="chat-box" ref="chatBox" class="chat-box">
			    <transition-group name="fade" mode="out-in">
				<div v-for="chat in chats" :key="chat.id" v-bind:class="{'chat chat-left': chat.from=='bot',  'chat chat-right': chat.from!='bot'}"  >
				  <p v-if="!chat.loading">{{chat.message}}</p>
				  <p v-if="chat.loading">
				    <svg width="50" height="30">
				        <circle cx="10" cy="20" r="5" stroke="black" stroke-width="3" fill="black">
				        <animate attributeType="CSS" attributeName="opacity" from="1" to="0" begin="0s" dur="1s" repeatCount="indefinite" />
				        </circle>
				        <circle cx="25" cy="20" r="5" stroke="black" stroke-width="3" fill="black">
				        <animate  attributeType="CSS" attributeName="opacity" from="1" to="0" begin="0.3s" dur="1s" repeatCount="indefinite" />        
				        </circle>
				        <circle cx="40" cy="20" r="5" stroke="black" stroke-width="3" fill="black">
				        <animate  attributeType="CSS" attributeName="opacity" from="1" to="0" begin="0.6s" dur="1s" repeatCount="indefinite" />        
				        </circle>
					</svg>
				  </p>

				</div>
				</transition-group>
			</div>
			<div class="chat-input">
				<input type="text" ref="message" v-on:keyup.enter="sendMessage" v-model="message" placeholder="Type here">
				<button class="send-button" @click="sendMessage"><i class="fa fa-play"></i></button>
	  		</div>  
		</div>
	</div>
	</transition>
	<button class="open-chat-button" @click="togglePopup()">Chat</button>
	</div>

</template>
<script>

import axios from 'axios';
//import RiveScript from 'rivescript';
export default {
  name: 'home',
  data() {
      return {
      bot : null,
      chats:[
      ],
      message :"",
      id : 0,
      showpopup: false,
      loading:false,
      username : "local-user",
      }
    },
    mounted(){
    	//this.bot = new RiveScript();
    	//this.bot.loadDirectory("../assets/brain").then(this.loading_done).catch(this.loading_error);
        this.chats.push({
	    	id:this.id,
	      	from:"bot",
	      	loading:false,
	      	message:"Hello, Welcome"
    	})
        this.id += 1
    },
    updated(){              
	  	var container = this.$el.querySelector("#chat-box");
      	container.scrollTop = container.scrollHeight;
	},	
    methods:{
    sendMessage(){
    	this.loading = true
        // Me
    	this.chats.push({
	    	id:this.id,
	      	from:"me",
	      	loading:false,
	      	message:this.message
    	})
    	this.id += 1
    	//Send Request
        var data = {
	    	id:this.id,
	      	from:"me",
	      	message:this.message
    	}    	
    	this.chats.push({
		    	id:this.id,
		      	from:"bot",
		      	loading:true,
		      	message:"ddd"
	    })
	    this.id += 1 	
	    
    	axios.post("https://devpramitsawant.pythonanywhere.com/api/bot",data)   
        .then( response => {
        	this.updateMessage(response.data.message)     
        })
        .catch(
          error => {
            //console.log(error)
            error
            //this.loading = false
          }
        )
        
    	this.message = ""
    },
    updateMessage(message){
        //console.log(message,this.id)
    	this.chats[this.id-1].message = message
	    this.chats[this.id-1].loading = false
    },
    togglePopup(){
    	this.showpopup = !this.showpopup
    	this.$nextTick(() => this.$refs.message.focus())
    },
    /*
    loading_done() {
	  //console.log("Bot has finished loading!");
	  this.bot.sortReplies();
	  this.bot.reply(this.username, "Hello, bot!").then(function(reply) {
	  	reply
	    //console.log("The bot says: " + reply);
	  });
	},
	loading_error(error, filename, lineno) {
		error
		filename
		lineno
	  //console.log("Error when loading files: " + error);
	}
	*/
    }

}
</script>

